import React from 'react'
import Helmet from 'react-helmet'

import Layout from '../components/layout'
import HeaderGeneric from '../components/HeaderGeneric'
import pic02 from '../assets/images/pic02.jpg'

class Projects extends React.Component {
  render() {

    return (
      <Layout>
        <Helmet title="أهم مشاریع / مبادرات المؤسسة" />
        <HeaderGeneric />
        <div id="main">
          <section id="content" className="main special">
            <span className="image main"><img src={pic02} alt="" /></span>
            <header className="major">
              <h1>أهم مشاریع / مبادرات المؤسسة</h1>
            </header>
            <span className="icon style2 fa-heartbeat"></span>
            <p>مبادرة صیدلیة الخیر 2017 ومازلت مستمرة للحالات المزمنة</p>
            <span className="icon style2 fa-heartbeat"></span>

            <p>مبادرة طبیب الخیر 2017 ومازلت مستمرة, تعاقد مع أطباء من تخصصات مختلفة ومتابعة مركزیة من خلال 
    المؤسسة للحالات</p>
            <span className="icon style2 fa-heartbeat"></span>
            <p>مبادرة معامل ومراكز اشعة الخیر, تعاون لتغطیة الحالات على مستوى الجمهوریة , بخصومات تصل الي %90</p>
            <span className="icon style2 fa-globe"></span>

            <p> مبادرة احنا معاكم لاطعام الاطباء والممرضین و حالات الكورونا بالعزل المنزلي و المتضررین و العمالة الغیر
                منتظمة خلال ازمة كورونا </p>
                <span className="icon style2 fa-globe"></span>

            <p>تدریب وتأهیل متطوعین قادرین على العمل المجتمعي و خدمة النفع العام</p>
            <span className="icon style2 fa-globe"></span>
            <p>تدریب وتأهیل فریق الاستجابة في حالات الطوارئ و فریق شباب أستشاري للمؤسسة</p>
            <span className="icon style2 fa-globe"></span>
            <p>أكادیمیة خیر (تحت الإنشاء - أكادیمیة تهدف إلى إحداث نقلة في قطاع العمل الإنساني حیث یصبح الأشخاص
                العاملون مع اللاجئین و قطاع المجتمع المدني مجهزین بالمعرفة والمهارات والخبرة)</p>
                <span className="icon style2 fa-child"></span>

            <p>تزوید الأفراد بإمكانیة الوصول إلى تعلیم رسمي وغیر رسمي عالي الجودة یستهدف المتسربین من المدارس
                والأطفال اللاجئین (مدارس مجتمعیة - حضانات - فصول مجتمعیة)</p>
                <span className="icon style2 fa-venus"></span>

            
            <p>مبادرات تعلم التكنولوجیا و الخیاطة والحرف الیدویة وصناعة الجلود و ادارة المشاریع الصغیرة ومتناهیة الصغر
                (تعزیز مكانة المرأة )</p>

          </section>
        </div>
      </Layout>
    )
  }
}

export default Projects
